/* Input.css */
.input-container {
  text-align: center;
  margin-bottom: 20px;
}

label {
  font-size: 24px;
  text-shadow: 2px 2px 8px black;
  color: #ffff00;
  /* neon yellow */


}

input[type='text'] {
  padding: 10px 20px;
  border-radius: 20px;
  border: none;
  font-size: 18px;
  outline: none;
  background-color: #000000;
  /* black */
  color: #ffff00;
  /* neon yellow */
}

button {
  background: #ffff00;
  /* neon yellow */
  color: #000000;
  /* black */
  border: none;
  border-radius: 20px;
  padding: 10px 20px;
  font-size: 18px;
  cursor: pointer;
  margin-left: 10px;
  outline: none;
}

button:hover {
  background: #000000;
  /* black */
  color: #ffff00;
  /* neon yellow */
}

button:focus {
  box-shadow: 0 0 0 2px #ffff00;
  /* neon yellow */
}

/* Media queries */
@media only screen and (max-width: 768px) {
  label {
    font-size: 20px;
  }

  input[type='text'] {
    font-size: 16px;
  }

  button {
    font-size: 16px;
    padding: 8px 16px;
  }
}

@media only screen and (max-width: 480px) {
  label {
    font-size: 16px;
  }

  input[type='text'] {
    font-size: 14px;
  }

  button {
    font-size: 14px;
    padding: 8px 12px;
  }
}